import { Component } from "react";
import './index.scss';
import { Input, Button, Checkbox, message } from 'antd';
import { GoogleLogo } from "@phosphor-icons/react";
import { signInWithEmailAndPassword, createUserWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import { json } from "react-router-dom";

class AccountForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      confirmPassword: '',
    };
  }

  handleInputChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  componentDidUpdate(prevProps) {
    if (this.props.accountFormType !== prevProps.accountFormType) {
      this.setState({
        email: '',
        password: '',
        confirmPassword: '',
      });
    }
  }

  handleAuth = () => {
    const { accountFormType, auth, navigate } = this.props;
    const { email, password, confirmPassword } = this.state;

    if (accountFormType === 'login') {
      signInWithEmailAndPassword(auth, email, password)
        .then(userCredential => {
          // Successfully signed in
          message.success('ng nhp thnh cng!');
          localStorage.setItem('currentUser', JSON.stringify(email))
          navigate('/')
        })
        .catch(error => {
          // Error signing in
          this.setState({ email: '', password: '', confirmPassword: '' });
          message.error('ng nhp tht bi!');
        });
    } else {
      if (password.trim().length < 8) {
        message.error('Mt khu phi ln hn 8 k t');
        this.setState({ email: '', password: '', confirmPassword: '' });
        return;
      }
      if (password !== confirmPassword) {
        message.error('Mt khu khng khp!');
        this.setState({ email: '', password: '', confirmPassword: '' });
        return;
      }
      createUserWithEmailAndPassword(auth, email, password)
        .then(userCredential => {
          // Successfully registered
          message.success('ng k thnh cng!');
          localStorage.setItem('currentUser', JSON.stringify(email))
          navigate('/')
        })
        .catch(error => {
          // Error registering
          this.setState({ email: '', password: '', confirmPassword: '' });
          message.error('ng k tht bi');
        });
    }
  };

  handleGoogleSignIn = () => {
    const { auth, navigate } = this.props;
    const provider = new GoogleAuthProvider();
    signInWithPopup(auth, provider)
      .then(result => {
        // Successfully signed in with Google
        const user = result.user;
        const email = user.email;
        message.success('ng nhp vi Google thnh cng!');
        localStorage.setItem('currentUser', JSON.stringify(email));
        navigate('/');
      })
      .catch(error => {
        // Error signing in with Google
        message.error('ng nhp vi Google tht bi!');
      });
  };

  render() {
    const { accountFormType, setAccountFormType } = this.props;
    const { email, password, confirmPassword } = this.state;
    const title24 = accountFormType === 'login' ? "NG NHP VO TI KHON CA BN" : "NG K TI KHON MI";
    const title14 = accountFormType === 'login' ? "CHA L THNH VIN" : " C TI KHON";
    const textBtn = accountFormType === 'login' ? 'NG NHP' : 'NG K';
    const textLink = accountFormType === 'register' ? 'NG NHP' : 'NG K';

    return (
      <div className="account-wrapper">
        <img src="images/image-login.jpeg" alt="Login" />
        <div className="account-form">
          <p className="form-title title-size24">{title24}</p>
          <p className="form-title title-size14">
            {title14} <a onClick={() => setAccountFormType(textLink === 'NG NHP' ? 'login' : 'register')}>{textLink}</a>
          </p>
          <Input required className="input-account" placeholder="Tn ng nhp" name="email" value={email} onChange={this.handleInputChange} />
          <Input required className="input-account" placeholder="Mt khu" type="password" name="password" value={password} onChange={this.handleInputChange} />
          {accountFormType === 'register' && (
            <Input required className="input-account" placeholder="Nhp li mt khu" type="password" name="confirmPassword" value={confirmPassword} onChange={this.handleInputChange} />
          )}
          {accountFormType === 'login' && (
            <>
              <Checkbox className="check-box"> Nh mt khu</Checkbox>
              <a className="link-forget" href="#">Qun mt khu</a>
            </>
          )}
          <Button className="btn-login" onClick={this.handleAuth}>{textBtn}</Button>
          <h3>--hoc--</h3>
          <Button className="btn-login btn-google" onClick={this.handleGoogleSignIn}>
            ng nhp vi Google <GoogleLogo className="logo-google" size={32} weight="fill" />
          </Button>
        </div>
      </div>
    );
  }
}

export default AccountForm;
